import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, message, Col } from 'antd';
import { getListTrainingSystem, deleteTrainingSystem } from 'services/TrainingSystem';
// import { typeOfTraining } from 'dump/data';

class TrainingSystemPage extends Component {
  state = {
    dataTrainingSystem: [],
    loading: false,
    pagination: {},
    page: 0,
    IsPagingEnabled: true,
  };
  componentDidMount() {
    this.getListData();
  }

  getListData = (page, IsPagingEnabled) => {
    this.setState({
      loading: true,
    });
    getListTrainingSystem(page, IsPagingEnabled)
      .then(res => {
        const pagination = { ...this.state.pagination };
        pagination.total = res.data.totalResults;
        this.setState({
          dataTrainingSystem: res.data.items,
          loading: false,
          pagination,
        });
      })
      .catch(error => {
        console.log('====>', error);
        this.setState({
          loading: false,
        });
      });
  };

  handleTableChange = (pagination, filters, sorter) => {
    const { IsPagingEnabled } = this.state;
    this.setState({
      page: pagination.current,
    });
    this.getListData(pagination.current, IsPagingEnabled);
  };

  confirmDelete = e => {
    this.setState({
      loading: true,
    });
    deleteTrainingSystem(e)
      .then(() => {
        message.success('Xóa hệ đào tạo thành công');
        this.getListData();
        this.setState({
          loading: false,
        });
      })
      .catch(error => {
        this.setState({
          loading: false,
        });
        console.log('res', error);
        message.error(error.response.data.message);
      });
  };

  cancel = e => {};
  render() {
    const columns = [
      {
        title: 'Mã hệ',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên hệ',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Tên tiếng anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
      },
      {
        title: 'Quy chế',
        dataIndex: 'regularId',
        key: 'regularId',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) =>
          this.state.dataTrainingSystem.length >= 1 ? (
            <Fragment>
              <BtnEdit href={`#system-training/form/edit/${record.id}`} />
              <Popconfirm title="Sure to delete?" onConfirm={() => this.confirmDelete(record.id)}>
                <BtnDelete />
              </Popconfirm>
            </Fragment>
          ) : null,
      },
    ];
    return (
      <Card title="Hệ thống đào tạo">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/system-training/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              loading={this.state.loading}
              bordered
              dataSource={this.state.dataTrainingSystem}
              columns={columns}
              pagination={this.state.pagination}
              onChange={this.handleTableChange}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TrainingSystemPage />
  </Page>
);



